/* base */
@import url("https://fonts.googleapis.com/css2?family=Plus+Jakarta+Sans:ital,wght@0,400;0,500;0,600;0,700;0,800;1,400;1,500;1,600;1,700&family=Raleway:wght@400;500;600;700;800&display=swap");
* {
  padding: 0;
  margin: 0;
  outline: none;
  box-sizing: border-box; }

h1 {
  letter-spacing: -0.04em; }
  h1 * {
    letter-spacing: initial; }

:root {
  font-size: 62.5%; }

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0;
  transition: all 0.3s; }

ul {
  padding: 0;
  margin: 0; }
  ul li {
    list-style: none; }

img {
  width: 100%;
  max-width: 100%;
  height: auto; }

a {
  transition: all 0.3s;
  text-decoration: none; }
  a:hover {
    text-decoration: none; }

button {
  cursor: pointer;
  outline: none !important;
  border: none;
  background: transparent; }

svg,
path,
circle {
  transition: all 0.3s; }

input {
  outline: none;
  border: none;
  box-shadow: none; }
  input:focus {
    outline: none; }

img {
  width: auto;
  max-width: 100%;
  height: auto; }

html,
body {
  max-width: 100%;
  overflow-x: hidden; }

body {
  font-size: 16px;
  font-family: 'Raleway', sans-serif;
  background-color: var(--bg-primary-colour);
  color: var(--text-primary-colour);
  font-weight: 500; }

body.position-fixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%; }

.outer-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 4rem 0; }
  @media (max-width: 1239px) {
    .outer-wrapper {
      padding: 3.2rem 0; } }
  @media (max-width: 767px) {
    .outer-wrapper {
      padding: 2.4rem 0; } }

.container {
  margin: 0 auto; }

.container.full-container {
  width: 100%;
  max-width: 1920px;
  padding: 0 63px; }
  @media (max-width: 1239px) {
    .container.full-container {
      padding: 0 32px; } }
  @media (max-width: 767px) {
    .container.full-container {
      padding: 0 24px; } }

.icon {
  transition: all 0.3s;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
  flex-shrink: 0; }

.title {
  font-weight: 700;
  line-height: 130%;
  letter-spacing: -0.02em;
  color: var(--text-primary-colour); }

h1.title {
  font-size: 46px; }
  @media (max-width: 767px) {
    h1.title {
      font-size: 24px; } }

h2.title {
  font-size: 32px; }
  @media (max-width: 767px) {
    h2.title {
      font-size: 22px; } }

h3.title {
  font-size: 28px; }
  @media (max-width: 767px) {
    h3.title {
      font-size: 20px; } }

h4.title {
  font-size: 24px; }
  @media (max-width: 767px) {
    h4.title {
      font-size: 18px; } }

h5.title {
  font-size: 20px; }
  @media (max-width: 767px) {
    h5.title {
      font-size: 16px; } }

h6.title {
  font-size: 18px; }
  @media (max-width: 767px) {
    h6.title {
      font-size: 16px; } }

.body-paragraph {
  letter-spacing: -0.01em;
  line-height: 160%;
  color: var(--text-secondary-colour); }

.body-xl {
  font-size: 24px; }

.body-m {
  font-size: 18px; }

.body-s {
  font-size: 16px; }

.body-xs {
  font-size: 14px; }

.bold {
  font-weight: 700; }

.regular {
  font-weight: 400; }

.pb18 {
  padding-bottom: 18rem; }
  @media (max-width: 767px) {
    .pb18 {
      padding-bottom: 12rem; } }

@media (min-width: 1280px) {
  .container {
    max-width: 1240px;
    padding-right: 0;
    padding-left: 0; } }

@media (max-width: 767px) {
  .container {
    padding-right: 20px;
    padding-left: 20px; } }

