:root {
  --text-primary-colour: #000000;
  --link-primary-colour: #6E1F8A;
  --bg-primary-colour: #FFFFFF; }

/* page */
.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between; }
  .header__inner .site-logo {
    display: block;
    max-width: 126px;
    width: 100%;
    height: 45px; }

.header {
  margin-bottom: 48px; }
  @media (max-width: 768px) {
    .header {
      margin-bottom: 32px; } }

.menu-list {
  display: flex;
  align-items: center; }
  .menu-list .menu-list__item:not(:last-child) {
    margin-right: 16px; }
  .menu-list .menu-list__item .menu-list__item-link {
    font-weight: 600;
    font-size: 14px;
    line-height: 16px;
    color: var(--link-primary-colour);
    display: block; }

.cooming-soon {
  margin: 0 auto; }

.cooming-soon__title {
  max-width: 770px;
  margin: 0 auto;
  font-weight: 700;
  font-size: 46px;
  line-height: 130%;
  text-align: center;
  letter-spacing: -0.02em;
  margin-bottom: 32px; }
  @media (max-width: 767px) {
    .cooming-soon__title {
      font-size: 32px; } }

.cooming-soon__subtitle {
  font-weight: 500;
  font-size: 24px;
  line-height: 160%;
  text-align: center;
  letter-spacing: -0.01em;
  margin-bottom: 32px; }

.cooming-soon__image {
  max-width: 770px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto; }

/* default */
